


html {
    width: 100%;
    margin: 0;
}

.center {
    margin: 0 auto;
}


.login {
    background-color: white;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: auto auto 1fr;
    height: 100vh;


    header {
        grid-column: 1 / 5;
        grid-row: 1 / 2;
        width: 100%;
        display: flex;
        flex-flow: row nowrap;
        justify-content: space-between;
        align-items: center;
        position: fixed;
        top: 0;
        z-index: 9999;
        box-sizing: none;
        box-shadow: 0 4px 2px -2px rgba(0, 0, 0, 0.084);


        strong {
            font-size: 2.2rem;
            padding-left: 15px;
        }

        em {
            color: green;
        }

        nav {
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-flow: row nowrap;

            align-self: center;


            ul {
                display: flex;
                justify-content: space-between;
                align-items: center;

                li {
                    list-style: none;
                    display: inline-flex;
                    justify-content: space-between;
                    padding: 10px;
                    margin: 10px;
                    font-size: 1.5rem;


                    &:hover {
                        color: black;
                        background-color: white;
                    }

                    &:visited {
                        color: black;

                    }


                }


            }
        }

        #sign-in {
            background-color: green;

            border: none;
            padding: 20px;
            width: 150px;
            border-radius: 50vh;
            color: white;
            font-size: 1.2rem;
            margin-right: 15px;
            transition: 500ms;

            &:focus {
                transform: scale(.9);
                box-shadow: 0 0 20px black;


            }
        }

    }


}


.login__main {
    display: flex;
    grid-column: 1 / 5;
    grid-row: 2 / 3;
    width: 80%;
    flex-flow: column nowrap;

    h2 {
        font-size: 4rem;
        margin-left: 50px;
    }

    p {
        font-size: 1.5rem;
        margin-bottom: 2rem;
    }

    .login__main-login {
        display: flex;
        flex-flow: column nowrap;
        margin-top: 15vh;
        width: 80%;

        form {
            display: flex;
            justify-content: center;
            flex-flow: column nowrap;

            



            input {
                margin: 25px;
                box-sizing: border-box;
                border: 2px solid black;
                font-size: 1.5rem;
                padding: 20px;
                width: 35vw;
                border-radius: 50vh;
                box-shadow: none;
                align-self: center;
            }







            .login__main-login-btn {
                background-color: green;
                font-size: 1.2rem;
                border: none;
                padding: 20px;
                width: 150px;
                border-radius: 50vh;
                color: white;
                align-self: flex-end;
                margin-top: 2vh;
                margin-right: 15px;
                transition: 500ms;

                &:focus {
                    transform: scale(.9);
                    box-shadow: 0 0 20px black;


                }

                &:hover {
                    box-shadow: 2px 2px 2px 15px rgba(0, 0, 0, 0.342);
                    transform: translateY(-5px)
                }
            }
        }

            #register-link {
                margin-left: 50px;
                font-size: 1.2rem;

            }

    }

}





.acc-card {
    border: 2px solid white;
    border-radius: .5rem;
    width: auto;
}